body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.SpreadSheet,
.FlowingList {
  list-style: none;
  margin: 0;
  padding-inline-start: 0;
  margin-block-start: 0;
  margin-block-end: 0; }
  .SpreadSheet li,
  .FlowingList li {
    list-style: none;
    display: inline-block;
    width: 100px;
    text-overflow: ellipsis;
    white-space: nowrap; }
    .SpreadSheet li button,
    .FlowingList li button {
      width: 100%;
      padding: 8px;
      background-color: transparent;
      text-align: inherit; }
      .SpreadSheet li button:hover,
      .FlowingList li button:hover {
        background-color: #666; }
      .SpreadSheet li button.active,
      .FlowingList li button.active {
        background-color: #333; }

.FlowingList li {
  display: block;
  width: auto; }

.blinking {
  animation: smooth-blink 3s linear infinite; }

@keyframes blink {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  50.01% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes smooth-blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; } }

li.BookmarkRow > button {
  width: calc(100% - 40px);
  display: inline-block;
  padding: 10px; }

li.BookmarkRow > div {
  width: 40px;
  display: inline-block; }
  li.BookmarkRow > div button {
    text-align: center; }
    li.BookmarkRow > div button * {
      pointer-events: none; }
